<template>
  <div>
    <div class="operButton">
        <el-button size="small" type="success" @click="add">添加</el-button>
        <el-button size="small" type="success" :disabled="selectedRow.length == 0" @click="delRow">删除</el-button>
        <el-button size="small" type="success" @click="exportData">导出</el-button>
      </div>
    <el-card class="box-card" style="margin: 10px 10px">
      <el-table :data="tableData" stripe style="width: 100%" highlight-current-row @current-change="selectRow">
        <el-table-column prop="pointManNo" label="指示者编号" width="180">
        </el-table-column>
        <el-table-column prop="name" label="姓名" width="180"> </el-table-column>
        <el-table-column prop="sex" label="性别" width="120"> </el-table-column>
        <el-table-column prop="idCardNo" label="身份证号" width="200">
        </el-table-column>
        <el-table-column prop="addr" label="住址"> </el-table-column>
      </el-table>
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20, 30, 50]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
      <add-win v-if="addFlag"></add-win>
    </el-card>
  </div>
</template>
<script>
import AddWin from "./Add.vue";
export default {
  components: {
    AddWin
  },
  data() {
    return {
      addFlag: false,
      total: 0,
      currentPage: 1,
      pageSize: 10,
      tableData: [],
      selectedRow: []
    };
  },
  mounted() {
    this.getList();
  },
  methods: {
    getList() {
      let param = {
        page: this.currentPage,
        pageSize: this.pageSize,
        field: "name"
      };
      let paramStr = "";
      Object.keys(param).forEach(item => {
        paramStr += "&" + item + "=" + param[item];
      });
      this.axios({
        method: "get",
        url: "http://10.72.89.1:8050/qs/api/v1/querypm?" + new Date().getTime() + paramStr
      }).then(response => {
        this.tableData = response.data.data;
        this.total = response.data.total;
      }).catch(error => {
        this.loading = false;
        this.$message.error(error);
        console.log(error); //请求失败返回的数据
      });
    },
    add() {
      this.$router.push({ path: "/Add" });
    },
    handleCurrentChange(page) {
      this.currentPage = page;
    },
    handleSizeChange(val) {
      this.pageSize = val;
    },
    selectRow(row) {
      this.selectedRow = row;
    },
    delRow() {
      if (this.selectedRow) {
        let param = {
          pmid: this.selectedRow.id
        };
        let paramStr = "";
        Object.keys(param).forEach(item => {
          paramStr += "&" + item + "=" + param[item];
        });
        this.axios({
          method: "post",
          data: JSON.stringify({}),
          headers: {
            "Content-Type": "application/json"
          },
          url: "http://10.72.89.1:8050/qs/api/v1/deletepm?" + new Date().getTime() + paramStr
        }).then(response => {
          if (response.data.code == 0) {
            this.$message.success("删除成功！");
            this.getList();
          } else {
            this.$message.error(response.data.code);
          }
        }).catch(error => {
          this.$message.error(error);
          console.log(error); //请求失败返回的数据
        });
      }
    },
    exportData() {
      document.location = "http://10.72.89.1:8050/qs/download";
    }
  }
};
</script>
<style scoped>
.operButton {
  text-align: left;
  padding-left: 10px;
  margin-top: 10px;
}
</style>



