import Vue from 'vue'
import Router from 'vue-router'
import List from '@/components/List.vue'
import Add from '@/components/Add.vue'
import Login from '@/components/Login.vue'

Vue.use(Router)

export default new Router({
  routes: [
    // {
    //   path: '/',
    //   name: 'Login',
    //   component: Login
    // },
    {
      path: '/',
      name: 'List',
      component: List
    },
    {
      path: '/Add',
      name: 'Add',
      component: Add
    }
  ]
})



